<template>
  <v-container class="py-5">
    <v-card>
      <v-card-title>
        Search PP Record
        <v-spacer></v-spacer>
        <!-- <v-btn v-if="!addLink || !addLink.hide" :class="addLink ? addLink.class || 'primary' : 'primary'" :to="addLink ? addLink.url : '/resource/add/'+page">
          <v-icon v-if="addLink && addLink.icon">{{ addLink.icon }}</v-icon>
          <v-icon v-else>mdi-database-plus</v-icon>
          {{ $t("App.hardcoded-texts.add") }} {{$t(`App.fhir-resources-texts.${label}`)}}
        </v-btn> -->
      </v-card-title>
      <v-card-title>
        <v-text-field label="Name" class="ma-2" v-model="search.name" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('name')" />
        <v-text-field label="HP Number" class="ma-2" v-model="search.hpnumber" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('hpnumber')" />
        <v-text-field label="Omang Number" class="ma-2" v-model="search.omang" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('omang')" />
        <v-text-field label="Passport Number" class="ma-2" v-model="search.passport" dense outlined hide-details clearable @change="buildTerms()" @click:clear="clearSearch('passport')" />
      </v-card-title>
      <v-card-subtitle
        v-if="error_message"
        class="white--text error"
      >{{ error_message }}</v-card-subtitle>
      <v-card-text>
        <v-data-table
          style="cursor: pointer"
          :headers="headers"
          :items="results"
          item-key="id"
          :options.sync="options"
          :server-items-length="total"
          :footer-props="{ 'items-per-page-text':$t('App.hardcoded-texts.tableText'), 'items-per-page-options': [5,10,20,50] }"
          :loading="loading"
          class="elevation-1"
          @click:row="clickIt"
        ></v-data-table>
      </v-card-text>
    </v-card>

  </v-container>
</template>

<script>
export default {
  name: "ihris-search",
  data: function() {
    return {
      search: {
        name: '',
        hpnumber: '',
        omang: '',
        passport: ''
      },
      debug: "",
      terms: {},
      headers: [{
        text: "Surname",
        value: "surname"
      }, {
        text: "Given Name(s)",
        value: "given"
      }, {
        text: "Birth Date",
        value: "birthDate"
      }, {
        text: "Gender",
        value: "gender"
      }, {
        text: "Board",
        value: "board"
      }, {
        text: "HP Number",
        value: "hpnumber"
      }, {
        text: "OMANG",
        value: "omang"
      }, {
        text: "Passport",
        value: "passport"
      }],
      results: [],
      options: { itemsPerPage: 10 },
      loading: false,
      total: 0,
      prevPage: -1,
      link: [],
      error_message: null,
      update_again: { rerun: false, restart: false }
    };
  },
  watch: {
    options: {
      handler() {
        this.getData();
      },
      deep: true
    }
  },
  mounted: function() {
    this.getData(true);
  },
  methods: {
    clearSearch: function(field) {
      this.search[field] = ''
      this.buildTerms()
    },
    clickIt: function(record) {
      this.$router.push({
        name: "resource_view",
        params: { page: 'bwpractitioner', id: record.id }
      });
    },
    checkRerun() {
      if ( !this.loading && this.update_again.rerun ) {
        this.getData( this.update_again.restart )
        this.update_again = { rerun: false, restart: false }
      }
    },
    async buildTerms() {
      this.terms = {}
      if(this.search.name) {
        this.terms["name:contains"] = this.search.name
      }
      if(this.search.omang) {
        this.terms.identifier = this.search.omang
      }
      if(this.search.passport) {
        this.terms.identifier = this.search.passport
      }
      if(this.search.hpnumber) {
        await this.getDataBasic()
      }
      this.getData(true);
    },
    getDataBasic() {
      return new Promise((resolve, reject) => {
        let url = "/fhir/Basic?hpnumber=" + this.search.hpnumber
        fetch(url).then(response => {
          response.json().then(async (data) => {
            for (let entry of data.entry) {
              let practitioner = entry.resource.extension.find((ext) => {
                return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
              })?.valueReference?.reference
              if(!practitioner) {
                continue
              }
              this.terms["_id"] = practitioner
            }
            resolve()
          }).catch(() => {
            reject()
          })
        }).catch(() => {
          reject()
        })
      })
    },
    getData(restart) {
      //console.log("getting data",restart)
      if ( this.loading ) {
        this.update_again.rerun = true
        this.update_again.restart = this.update_again.restart || restart
        return
      }
      this.loading = true;
      this.error_message = null;
      let url = "";
      if (restart) this.options.page = 1;
      if (this.options.page > 1) {
        if (this.options.page === this.prevPage - 1) {
          url = this.link.find(link => link.relation === "previous").url;
        } else if (this.options.page === this.prevPage + 1) {
          url = this.link.find(link => link.relation === "next").url;
        }
        // Should make this smarter to keep the _getpages parameter,
        // but the issue is with tracking permissions on the resource
        url = url.replace(/_getpages=[^&]*&*/, "").replace("/fhir?","/fhir/Practitioner?")
        url = url.substring(url.indexOf("/fhir/"));

        //some of the hapi instances requires _total=accurate to always be available for them to return total resources
        if(url.indexOf('_revinclude=Basic:practitioner') === -1) {
          url = url + '&_revinclude=Basic:practitioner'
        }
        if(url.indexOf('_tag=pp') === -1) {
          url = url + '&_tag=pp'
        }
        if(url.indexOf('_sort=-_lastUpdated') === -1) {
          url = url + '&_sort=-_lastUpdated'
        }
        if(url.indexOf('_total=accurate') === -1) {
          url = url + '&_total=accurate'
        }
      }
      if (url === "") {
        let count = this.options.itemsPerPage || 10;
        let sort = "";
        for (let idx in this.options.sortBy) {
          if (sort) {
            sort += ",";
          }
          if (this.options.sortDesc[idx]) {
            sort += "-";
          }
          sort += this.options.sortBy[idx];
        }
        url =
          "/fhir/Practitioner?_count=" +
          count +
          "&_revinclude=Basic:practitioner&_tag=pp&_sort=-_lastUpdated&_total=accurate"
        this.debug = url;
      }
      let sTerms = Object.keys(this.terms);
      for (let term of sTerms) {
        if ( Array.isArray( this.terms[term] ) ) {
          if ( this.terms[term].length > 0 ) {
            url += "&" + term + "=" + this.terms[term].join(',')
          }
        } else if ( this.terms[term] ) {
          url += "&" + term + "=" + this.terms[term];
        }
      }

      this.prevPage = this.options.page;
      //console.log("fetching",url)
      fetch(url).then(response => {
        response.json().then(async (data) => {
          this.results = [];
          if (data.total > 0) {
            this.link = data.link;
            for (let entry of data.entry) {
              if(entry.resource.resourceType !== 'Practitioner') {
                continue
              }
              let result = { id: entry.resource.id };
              result.surname = entry.resource.name[0].family
              result.given = entry.resource.name[0].given.join(", ")
              result.birthDate = entry.resource.birthDate
              result.gender = entry.resource.gender
              result.gender = await this.$fhirutils.lookup( result.gender, 'http://hl7.org/fhir/administrative-gender' )
              let bwboards = entry.resource.meta.tag.filter((tag) => {
                return tag.system === 'http://ihris.org/fhir/CodeSystem/bw-boards'
              })
              let boards = []
              for(let board of bwboards) {
                boards.push(board.display)
              }
              result.board = boards.join(", ")
              result.omang = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='omang').value" );
              result.passport = this.$fhirpath.evaluate( entry.resource, "Practitioner.identifier.where(type.coding.code='passport').value" );
              let hpnumbers = []
              for (let entry of data.entry) {
                if(!entry.resource.meta.profile.includes("http://ihris.org/fhir/StructureDefinition/bhpc-registration-profile")) {
                  continue
                }
                let practitioner = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference'
                })?.valueReference?.reference
                if('Practitioner/' + result.id !== practitioner) {
                  continue
                }
                let hp = entry.resource.extension.find((ext) => {
                  return ext.url === 'http://ihris.org/fhir/StructureDefinition/bhpc-hp-number'
                })
                if(hp) {
                  hpnumbers.push(hp.valueString)
                }
              }
              result.hpnumber = hpnumbers.join(", ")
              this.results.push(result);
            }
          }
          this.total = data.total;
          this.loading = false;
          this.checkRerun()
        }).catch(err => {
          this.loading = false;
          this.error_message = "Unable to load results.";
          this.checkRerun()
          console.log(err);
        });
      }).catch(err => {
        this.loading = false;
        this.error_message = "Unable to load results.";
        this.checkRerun()
        console.log(err);
      });
    }
  }
};
</script>
<style>
tbody tr:nth-of-type(even) {
  background-color: #E0F2F1;
}
</style>
